import React from "react";
import { Grid, useMediaQuery } from "@material-ui/core"
import { useTheme } from "@material-ui/core/styles"
import ReactHtmlParser from "react-html-parser";

const Testimonial1 = ({ LandingPages }) => {
  const theme = useTheme()
  const recorrerLandingNuestaE = LandingPages.filter((filtrar) => {
    return filtrar.TipoLanding === 1;
  });
  const isMobile = useMediaQuery(theme.breakpoints.down("xs"))
  return (
    <div className="section" id="testimonial2">
      <div className="container" >
        <Grid
          container
          spacing={2}
          direction="row"
          justify="center"
          alignItems="center"
        >
          {recorrerLandingNuestaE.map((item, index) => (
            <div key={index.toString()}>{ReactHtmlParser(isMobile ? item.CuerpoPajinaMovil : item.CuerpoPagina)}</div>
          ))}
        </Grid>
      </div>
    </div>
  );
};

export default Testimonial1;
